@import "./media.scss";

main
{

    a 
    {
        text-decoration: none;
    }
    a:hover
    {
        text-decoration: underline;
    }
    a:visited
    {
        //color: #1A0DAB;
        color :   #457bfb;
        
    }
    .Heading
    {
        margin-top: 2rem;
        text-align: center;

        .Heading-submissions
        {
            font-size: large;
            text-decoration: underline;
            .SpaceForSp
            {
                display: none;
            }

            @include sp
            {
                .SpaceForSp
                {
                    display: inline;
                }
            }
        }

        .Heading-title
        {
            font-size: xx-large;
            hyphens: none;
            overflow-wrap:break-word;

            .Nonbreak
            {
                display: inline-block;
            }
        }

        @include sp
        {
            .Heading-title
            {
                font-size: 1.5rem;
                margin-top: 1rem;
            }
        }
        .Heading-author
        {
            margin: 1rem 0rem;
            font-size: large;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
        }

        .Heading-affiliation
        {
            color: #666666;
        }


        .Heading-images
        {
            margin: 1.0rem 0rem;

            display: flex;        
            align-items: center;  
            //flex-wrap: wrap;
            justify-content: center;
            
            Img
            {
                max-width: 40%;
                height: auto;
                margin-left: 1rem;
            }

            // .jpg-SO Img
            // {
            //     max-width: 50%;
            //     height: auto;
            // }
            // .jpg-US Img
            // {
            //     max-width: 100%;
            //     height: auto;
            // }
        }

        @include tab
        {
            // .Heading-images
            // {
            //     flex-direction: column;
                
            //     Img
            //     {
            //         max-width: 50%;
            //         width: 100%;
            //         height: auto;
            //         margin-bottom: 0.5rem;
            //     }
            // }
        }

        @include sp
        {
            .Heading-images
            {
                flex-direction: column;
                align-items: baseline;
                justify-content: center;

                Img
                {
                    max-width: 100%;
                    height: auto;
                    margin-bottom: 0.5rem;
                }
            }
        }
    }
    
    h2
    {
        color: #00a497;
        //color: #007f89;
        //color: #A05359;
        margin: 0.5rem 0rem;
    }
    ul
    {
        line-height: 2;
        margin-left: 2rem;
    }
    hr
    {
        height: 1px;
        margin-top: 1rem;
        background-color: #d3d3d3;
        //background-color: #f1e1e1;
        border: none;
    }
    div
    {
        hyphens: auto;
        line-height: 1.5;
    }

    .Abstract, .Acknowledgement
    {
        text-align: justify;
    }
    .Abstract
    {
        .Space
        {
            margin: 0.5rem 0rem;
        }
    }
    .SupplementalVideo 
    {
        .SupVideo
        {
            margin: 1rem 0rem;
            text-align: center;
        }
    }
    @include sp
    {
        .SupVideo {
            position: relative;
            width: 100%;
            padding-top: 56.25%;
            overflow:auto; 
            -webkit-overflow-scrolling:touch;
            //border:2px solid #ccc; 
          }
          .SupVideo iframe {
              position:absolute;
              top:0;
              left:0;
              width:100%;
              height:100%;
              border:none;
              display:block;
          }
    }
}

footer
{
    margin: 1rem 0rem;

    font-size: small;
    text-align: center;
    //background-color: #fff8ff;
    padding: 1rem 0rem 0rem 0rem;

    border-top: 1px solid #d3d3d3;
}
